<template>
  <div class="contacts pt-5">
    <div class="content ">
      <div class="container mt-5">
        <div class="contacts-block text-center mb-3">
          <div class="contacts-block__title">
            {{ $t('contacts.title') }}
          </div>
        </div>
        <b-row> 
          <b-col
            align-self="center"
            xl="6"
            lg="6"
          >
            <p class="contacts-text">
              {{ $t('contacts.subtitle') }}
            </p>
            <div class="contacts-adress text-center">
              <div class="contacts-adress__title mb-3">
                {{ $t('contacts.address1') }}
              </div>
              <div class="contacts-adress__text">
                FIRMA AKTIV TAXI
                Olpener Str. 690
                51109 Köln
              </div>
            </div>
          </b-col>
          <b-col
            class="my-4"
            xl="6"
            lg="6"
          >
            <div class="contacts-form">
              <div class="contacts-form__title text-center py-3">
                {{ $t('contacts.form.title') }}
              </div>
              <b-form
                class="px-4"
              >
                <div class="text-center pb-3">
                  <div
                    class="custom-dropdown "
                  >
                    <select
                      v-model="form.state"
                      required
                      class="form-control"
                    >
                      <option
                        
                        disabled
                        value=""
                      >
                        {{ $t('contacts.form.subject.one') }}
                      </option>
                      <option>{{ $t('contacts.form.subject.two') }}</option>
                      <option>{{ $t('contacts.form.subject.three') }}</option>
                      <option>{{ $t('contacts.form.subject.four') }}</option>
                      <option>{{ $t('contacts.form.subject.five') }}</option>
                    </select>
                  </div>
                </div>
                
                <label
                  required
                  for="inp"
                  class="inp my-2"
                >
                  <input
                    id="inp"
                    v-model="form.name"
                    type="text"
                    required
                    placeholder="   "
                  >
                  <span class="label"> {{ $t('contacts.form.name') }}</span>
                  <span class="border1" />
                </label>
                
                <label
                  required
                  for="inp2"
                  class="inp my-2"
                >
                  <input
                    id="inp2"
                    v-model="form.email"
                    type="email"
                    required
                    placeholder="   "
                  >
                  <span class="label">Ihre E-Mail-Adresse (Pflichtfeld)</span>
                  <span class="border1" />
                </label>
                <label
                  for="inp3"
                  class="inp my-2"
                >
                  <textarea
                    id="inp3"
                    v-model="form.text"
                    required
                    placeholder="   "
                  />
                  <span class="label">{{ $t('contacts.form.msg') }}</span>
                  <span class="border1" />
                </label>
                <div>
                  <vue-recaptcha
                    ref="recaptcha"
                    class="captcha"
                    :sitekey="sitekey"
                    @verify="onVerify"
                  />
                </div>
                <div class="text-center py-3">
                  <b-button
                    variant="contacts"
                    size="sm"
                    @click="onSubmit() "
                  >
                    {{ $t('contacts.form.send') }}
                  </b-button>
                </div>
              </b-form>
            </div>
          </b-col>
        </b-row>
      </div>
    </div>
    <Footer />
  </div>
</template>

<script>
import axios from "axios";
import Footer from "@/components/Footer.vue";
import VueRecaptcha from "vue-recaptcha";
import i18n from '../i18n';
export default {
    name: "Contacts",
    components: {
        Footer,
        VueRecaptcha,
    },
    data() {
        return {
            sitekey: '6Ldrp84UAAAAAETWXfam5GoCdEVYktGcCVei4iGe',
            form: { 
                state: '',
                name: '',
                email: '',
                text: '',
                captcha: ''
            },
        }
    },
    methods: {
        onSubmit() {
            
            axios
                .post("http://192.168.0.123/api/contact/", {
                    title: this.form.state,
                    name: this.form.name,
                    email: this.form.email,
                    message: this.form.text,
                    captcha: this.form.captcha
                })
                .then(response => console.log(response))
        },
        onVerify: function (response) {
            this.form.captcha = response
        },
    },
    metaInfo: {
        title: i18n.t('contacts.title') ,
        titleTemplate: '%s / Aktiv Taxi Köln',
        htmlAttrs: {
            lang: `${i18n.locale}`,
            amp: true
        }
    }

};

</script>
